.App {
  text-align:center;
}


.App-header,.mainPage {
  background-image: linear-gradient(0deg,#225ca24d,#225ca210),url(../backgroundHome.jpg);
  background-size:cover;
  color: '#225ca210';
  min-width: 100vw;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items:center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-login-input
{
  width:40vw;
  height:3vh;
  background-color: rgba(255, 255, 255, 0.358);
  border-radius:10px;
  color:white;
  transition: all 0.6s;
}

.App-login-input:hover
{
  background-color:white;
  color:black;
  border-radius: 8px;
  transition: all 0.6s;
}
.App-login-input:focus
{
  background-color:white;
  color:black;
  transition: all 0.6s;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.authenticationText
{
  font-size: 2.5vh;
  color: white;
}

.App-login-button
{
  width:fit-content;
  height:fit-content;
  font-size:2.2vh;
  color:white;
  border-radius: 8px;
  padding:4px;
  border: 1px solid black;
  background-color:rgba(255, 255, 255, 0.358);
  transition: all 0.6s;
}
.App-login-button:hover 
{
  background-color: white;
  color: black;
  transition: all 0.6s;
}

.authForm
{
  background: linear-gradient(#425A79,#425A79,#425A79,#1a2d4e50,#15203d61);
  padding-left: 2vw;
  padding-right: 2vw;
  padding-top: 2vh;
  padding-bottom: 2vh;
  border-radius:12px;
  align-items:center;
  width:fit-content;
  height:50%;
  transition:all 0.6s;
}
.authForm p, .loginForm label, .registerForm label
{
  align-self:center;
  color:white;
  font-size:2.1vh;
}


.authForm input
{
  width:90%;
  height:2.5vh;
  background-color: rgba(255, 255, 255, 0.358);
  border-radius:6px;
  color:white;
  transition: all 0.6s;
}
.authForm img
{
  width: 10vw;
  height: 10vw;
}

.listItem
{
  transition: all 0.6s;
}

.listItem:hover
{
  background-color: #425A79;
  transition: all 0.2s;
}
